@import url("https://fonts.googleapis.com/css?family=Bebas+Neue|Open+Sans+Condensed:300,700&display=swap");
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans Condensed", sans-serif;
  color: #444;
  font-size: 1.2rem;
  font-weight: 300;
  padding: 0;
  margin: 0;
}

h1,
h2,
p span,
.btn {
  text-transform: uppercase;
}

h1 {
  font-family: "Bebas Neue", cursive;
  font-size: 4rem;
  color: #e50914;
  letter-spacing: 5px;
  display: inline-block;
  margin: 0.25em;
}

h2 {
  font-family: "Bebas Neue", cursive;
  font-size: 1.5rem;
  margin-bottom: 0.5em;
  letter-spacing: 3px;
}

p span {
  font-weight: 300;
  font-size: 1.8rem;
}

.btn {
  font-family: "Open Sans Condensed", sans-serif;
  font-size: 1.4rem;
  font-weight: 700;
  letter-spacing: 3px;
  display: inline-block;
  padding: 0.7em 1.7em;
  background: #e50914;
  color: #fff;
  text-decoration: none;
}
.btn:hover {
  background: #b40710;
}

.hero {
  background-color: #515151;
  background-image: url("/dist/img/bm.jpg");
  background-blend-mode: multiply;
  background-position: center center;
  background-size: cover;
  height: 50vh;
  padding: 1em;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.seasons {
  padding: 5vh 1em;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.season {
  flex-basis: 100%;
  padding: 1em 2em;
}

@media (min-width: 40rem) {
  .hero {
    height: 100vh;
  }

  .season {
    flex-basis: 80%;
    padding: 1em 2em;
  }
}
@media (min-width: 65rem) {
  .seasons {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 10vh 3em;
  }

  .season {
    flex-basis: 20%;
  }
}
.cta {
  background-color: #2b2b2b;
  padding: 10vh 0;
  color: #fff;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.cta-text {
  flex-basis: 100%;
  padding: 0 2em;
}
@media (min-width: 40rem) {
  .cta-text {
    flex-basis: 50%;
  }
}